import AttributeData from './AttributeData';
import { ViolenceAttributeGroups, AttributesPerGroup } from '../utils/EvolutionUtils';
import EvolutionGenderGroup from './EvolutionGenderGroup';

export default class EvolutionGenderData {
    constructor() {
        this.physical_violence = new AttributeData("Violencia Fsica");
        this.psychological_violence = new AttributeData("Violencia Psicolgica");
        this.economical_violence = new AttributeData("Violencia Econmica");
        this.sexual_violence = new AttributeData("Violencia Sexual");
        this.first_time = new AttributeData("1 vez agresin");
        this.aggr_alcohol = new AttributeData("Agresor ingiri alcohol");
        this.aggr_drugs = new AttributeData("Agresor us drogas");
        this.vict_alcohol = new AttributeData("Vctima ingiri alcohol");
        this.vict_drugs = new AttributeData("Vctima us drogas");
        this.vict_lgtbi = new AttributeData("Vctima relacionada a LGBTI+");
        this.vict_disability = new AttributeData("Vctima con discapacidad");
        this.inf = new AttributeData("Infantes");
        this.nin = new AttributeData("Nios");
        this.adol = new AttributeData("Adolescentes");
        this.adol_t = new AttributeData("Adolescentes tardos");
        this.jov = new AttributeData("Jvenes");
        this.adul = new AttributeData("Adultos");
        this.mayo = new AttributeData("Adultos mayores");
        this.family = new AttributeData("Vnculo Familiar");
        this.love = new AttributeData("Vnculo Amoroso");
        this.no_relation = new AttributeData("Sin Vnculo/Otro");
    }

    getGroupedData(){
        let response = new EvolutionGenderGroup();

        ViolenceAttributeGroups.forEach(group =>{
            let grouped = [];
            AttributesPerGroup[group].forEach(attribute => {
                grouped.push(this[attribute]);
            });
            response[group] = grouped;
        });

        return response;
    }
}